.App {
  text-align: center;
}

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;700;900&family=Raleway:wght@400;500;600;700;800;900&display=swap');
body{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Raleway', sans-serif;
}
img{
	max-width: 100%;
}
section.form-section {
    background: #b8d432;
    padding: 50px 0 350px;
    position: relative;
}
.container {
    max-width: 95%;
}
header {
    background: #050E19;
    padding: 10px;
}
nav.navbar.navbar-expand-sm.bg-dark.navbar-dark {
    background: #050E19 !important;
}
.navbar-dark .navbar-nav .nav-link {
    color: #fff;
    font-size: 24px;
    font-weight: 900;
}
span.site-color {
    color: #A5B926;
}
div#collapsibleNavbar {
    justify-content: center;
}
.form-box {
    background: #fff;
    padding: 40px;
    border-radius: 31px;
    position: relative;
    z-index: 1;
}
footer:before {
    width: 100%;
    height: 170%;
    background-size: 100% 100% !important;
    background-repeat: no-repeat;
    background: url(./images/jungle1.png);
    position: absolute;
    top: -140%;
    left: 0;
    content: "";
}
footer{
	position: relative;
}
.footer.text-center {
    height: auto;
    background-color: #000516;
    padding: 60px 0;
}
.footer  .container img.d-block.m-auto:first-child {
    max-width: 45%;
    margin-bottom: 40px !important;
    cursor: pointer;
    position: relative;
    z-index: 9;
}
.top-header-form h3 {
    font-size: 40px;
    color: #000;
    font-weight: 700;
    text-align: center;
    letter-spacing: 0.04em;
}
.connect-solana-wallet {
    background: #FF9900 !important;
    padding: 20px 100px !important;
    border-radius: 20px !important;
    border: 7px solid #B45F06 !important;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25) !important;
    font-size: 28px !important;
    color: #fff !important;
    font-weight: 700 !important;
    text-decoration: none !important;
    letter-spacing: 0.04em !important;
    display: inline-block !important;
    margin: 10px 0 !important;
    transition: .3s !important;
}
.connect-solana-wallet:hover {
    background: #4285F4 !important;
    border: 7px solid #4285F4 !important;
}
.top-header-form {
    text-align: center;
}
.form-box h4 {
    color: #474747;
    font-size: 36px;
    line-height: 40px;
    letter-spacing: 0.04em;
    font-weight: 500;
}
.form-body {
    padding: 30px 0 0;
}
.form-content-middle {
    text-align: center;
}
.quantity-text {
    color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
}
.quantity-text span {
    font-size: 47px;
    color: #474747;
    position: relative;
    top: 6px;
}
span.price {
    font-size: 24px;
    color: #7C7C7C;
    line-height: 28px;
}
h3.total-price {
    font-size: 36px;
    color: #4285F4;
    font-weight: 700;
    margin: 15px 0;
}
a.form-btn-footer {
    border-radius: 15px;
    background: #4285F4;
    padding: 20px 80px;
    display: inline-block;
    color: #fff;
    text-transform: uppercase;
    font-size: 28px;
    text-decoration: none;
    font-weight: 700;
    letter-spacing: 0.04em;
    line-height: 34px;
    margin-bottom: 50px;
    transition: .3s;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}



a.form-btn-footer:hover{
	background: #FF9900;
}

 

a.form3-btn-footer {
     
    background: #6AA84F !important;
    
     
}
 





.form-box h5 {
    font-size: 34px;
    color: #474747;
    font-weight: 600;
}
span.quantity-number{
    font-size: 80px;
    color: #000;
    top: 0;
}
.form-left-text-col .arrow-box:nth-child(2) {
    padding: 200px 0 70px;
}
.arrow-box img {
    display: block;
    margin: auto;
}
a {
    text-decoration: none;
}
.arrow-box1 img {
    padding-bottom: 20px;
    margin-top: -120px;
}
.arrow-box2 img {
    position: relative;
    left: 53px;
    top: 10px;
}
.arrow-box3 img {
    position: relative;
    left: 38px;
    top: 5px;
}



.top-header-form1 {
	line-height: 22px;
  }

  .top-header-form1 h3 {


	font-size: 45px;
	font-weight: 600;
	line-height: 40px;

  }

  .top-header-form1 h3 span {

	color: #4292F4;

  }

  .top-header-form1 p {

	font-size: 30px;
  }


  .rightcolcontent-form1 p {
	line-height: 20px;
	font-size: 23px !important;
	;


  }

  .rightcolcontent-form1 p:first-child,
  p:last-child {

	margin: 25px 0;


  }
/*media css*/
@media screen and (min-width: 1921px){
	.container{
		max-width: 1600px;
	}
}
