	.form-body .col-xl-4 {
	    flex: 0 0 auto;
	    width: 26.3%;
	}
	.form-body .col-xl-5 {
	    flex: 0 0 auto;
	    width: 48%;
	}
}

@media screen and (max-width: 1199px){
	.form-left-text-col .arrow-box:nth-child(2) {
	    padding: 0px 0 0px;
	}
	.arrow-box img {
	    margin-left: 0;
	}
	.arrow-box img {
	    width: 65px;
	    left: 0;
	}
	.arrow-box3 {
	    margin-top: 30px;
	}
	.arrow-box2 {
	    margin-top: 20px;
	}
	.arrow-box h4 {
	    font-size: 28px;
	    text-align: left;
	}
	.arrow-box1 img {
	    padding-bottom: 10px;
	    margin-top: 0;
	    width: 60px;
	    padding-left: 0;
	}
	.arrow-box.arrow-box1 {
	    margin-bottom:20px;
	}

	.form1_rightcol {
		width: 50% !important;
		margin: 0 auto;
		text-align: center;
	}
    .top-header-form1{
		margin-top: 20px;
	}
	.top-header-form1 h3{
		font-size: 40px !important;
	}



}
